import React from "react";
// import ReactDOM from "react-dom";
import PropTypes from "prop-types"; // Strict data type checking for props
import { Link } from "react-router-dom";
/**
 * A Functional Based Component
 */

function NavBar({ icon, title }) {
  return (
    <nav className="navbar bg-primary">
      <a href="/">
        <h1>
          <i className={icon}></i> {title}
        </h1>
      </a>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/about">About</Link>
        </li>
      </ul>
    </nav>
  );
}

// Default Props if props are not sent across
NavBar.defaultProps = {
  icon: "fab fa-github",
  title: "Github Seach Engine"
};

// propTypes when props are passed and indeed we do a strict data type check
NavBar.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired
};

