import React, { Fragment } from "react";
import axios from "axios";

//React Router
import { BrowserRouter, Route, Switch } from "react-router-dom";
// import ReactDOM from "react-dom";

import "./App.css";
import Navbar from "./components/Navbar";
import Users from "./components/Users";
import Search from "./components/Search";
import Alert from "./components/Alert";
import About from "./components/About";
import User from "./components/User";

import sampleUsers from "./data/sampleUsers.json";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      users: sampleUsers,
      user: {},
      loading: false,
      alert: null,
      repos: []
    };
    this.searchUsers = this.searchUsers.bind(this);
  }

  async componentDidMount() {
    this.setState({ loading: true });
    // const res = await axios.get("https://api.github.com/users");
    // this.setState({ users: res.data });
    this.setState({ loading: false });
  }

  async searchUsers(text) {
    // var index = this.state.users.findIndex(user => user.login === text);
    // var user = [this.state.users[index]];
    // this.setState({ users: user });

    this.setState({ loading: true });
    const res = await axios.get(
      `https://api.github.com/search/users?q=${text}`
    );
    this.setState({ users: res.data.items });
    this.setState({ loading: false });
  }

  clearUsers = () => {
    this.setState({ users: [] });
  };

  setAlert = (type, msg) => {
    this.setState({ alert: { type: type, msg: msg } });
    setTimeout(() => {
      this.setState({
        alert: null
      });
    }, 2000);
  };

  // Get Single user Data
  getUser = async username => {
    this.setState({ loading: true });
    const res = await axios.get(`http://api.github.com/users/${username}`);
    this.setState({ user: res.data, loading: false });
  };

  getUserRepos = async username => {
    this.setState({ loading: true });
    const res = await axios.get(
      `https://api.github.com/users/${username}/repos?per_page=5&created:asc`
    );
    this.setState({ repos: res.data, loading: false });
  };

  render() {
    return (
      <BrowserRouter>
        <div>
          {/*<Navbar title={{ a: "Github Search App" }} icon="fab fa-github" /> */}
          <Navbar />
          <div className="container">
            <Alert alert={this.state.alert} />
            <Switch>
              <Route
                path="/"
                exact
                render={() => (
                  <Fragment>
                    <Search
                      searchUsers={this.searchUsers}
                      clearUsers={this.clearUsers}
                      showClear={this.state.users.length ? true : false}
                      setAlert={this.setAlert}
                    />
                    <Users
                      users={this.state.users}
                      loading={this.state.loading}
                    />
                  </Fragment>
                )}
              />
              <Route path="/about" render={() => <About />} />
              <Route
                exact
                path="/user/:login"
                render={props => (
                  <User
                    {...props}
                    getUser={this.getUser}
                    user={this.state.user}
                    loading={this.state.loading}
                    repos={this.state.repos}
                    getUserRepos={this.getUserRepos}
                  />
                )}
              />
            </Switch>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
