import React from "react";
import { Link } from "react-router-dom";

// Stateless Components can be written as Functional Components
function UserItem({ user: { id, login, avatar_url, html_url } }) {
  return (
    <div className="card text-center" id={id}>
      <img
        className="round-img"
        src={avatar_url}
        alt=""
        style={{ width: "120px" }}
      />
      <h3>{login}</h3>
      <div>
        <Link
          to={`/user/${login}`}
          className="btn btn-dark btn-sm my-1"
          href={html_url}
        >
          GitHub Profile
        </Link>
      </div>
    </div>
  );
}

