import React, { Component } from "react";
import axios from "axios";

class About extends Component {
  constructor() {
    super();
    this.state = {
      fullName: { firstName: "", lastName: "" },
      password: "",
      primaryEmail: "",
      alternateEmail: "",
      mobile: "",
      dob: "",
      gender: "",
      location: "",
      lang: "",
      languages: [],
      cloud: {
        aws: false,
        gcp: false,
        azure: false,
        alibaba: false,
        ibm: false
      },
      bio: "",
      raise: 0,
      availability: ""
    };
  }

  onSubmit = async evt => {
    evt.preventDefault();
    console.log(this.state);
    const response = await axios.post(
      "https://getmybio.herokuapp.com/",
      this.state
    );
    console.log(response.data);
  };

  onClick = evt => {
    evt.preventDefault();
    this.setState(prevState => ({
      languages: [...prevState.languages, this.state.lang],
      lang: ""
    }));
  };

  onChange = evt => {
    console.log(evt.target.name, evt.target.value);

    if (evt.target.name === "firstName" || evt.target.name === "lastName") {
      this.setState(prevState => ({
        fullName: { ...prevState.fullName, [evt.target.name]: evt.target.value }
      }));
    } else if (evt.target.name === "cloud") {
      this.setState(prevState => ({
        cloud: { ...prevState.cloud, [evt.target.value]: true }
      }));
    } else {
      this.setState({ [evt.target.name]: evt.target.value });
    }
  };

  render() {
    return (
      <>
        <h1>About Github Search Engine</h1>
        <p>This app is built using React Technology</p>
        <br />
        <hr />
        <br />
        <p>This is a form with React and express</p>
        <div className="container" style={{ margin: "10px" }}>
          <form action="" onSubmit={this.onSubmit}>
            <label htmlFor="firstName">First Name</label>
            <input
              type="text"
              id="firstName"
              name="firstName"
              value={this.state.firstName}
              onChange={this.onChange}
            />
            <label htmlFor="lastName">Last Name</label>
            <input
              type="text"
              id="lastName"
              name="lastName"
              value={this.state.lastName}
              onChange={this.onChange}
            />
            <label htmlFor="password">Password</label>
            <input
              type="password"
              id="password"
              name="password"
              value={this.state.password}
              onChange={this.onChange}
            />
            <br />
            <label>Email Address</label>
            <br />
            <input
              type="email"
              name="primaryEmail"
              value={this.state.primaryEmail}
              onChange={this.onChange}
              placeholder="Primary Email Address"
            />
            <input
              type="email"
              name="alternateEmail"
              value={this.state.alternateEmail}
              onChange={this.onChange}
              placeholder="Alternate Email Address"
            />
            <br />
            <label htmlFor="mobile">Mobile Number</label>
            <br />
            <input
              type="tel"
              id="phone"
              // pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}"
              name="mobile"
              value={this.state.mobile}
              onChange={this.onChange}
            />
            <br />
            <label htmlFor="dob">Date of Birth</label>
            <br />
            <input
              type="date"
              name="dob"
              value={this.state.dob}
              onChange={this.onChange}
            />
            <br />
            <br />
            <label htmlFor="gender">Select your Gender </label>
            <br />
            <input
              type="radio"
              id="male"
              name="gender"
              value="male"
              onChange={this.onChange}
            />
            <label htmlFor="male" style={{ marginRight: "20px" }}>
              {" "}
              Male{" "}
            </label>
            <input
              type="radio"
              id="female"
              name="gender"
              value="female"
              onChange={this.onChange}
            />
            <label htmlFor="female"> Female </label>
            <br />
            <br />
            Select Your Location
            <select name="location" id="location" onChange={this.onChange}>
              <option value="bangalore">bangalore</option>
              <option value="chennai">chennai</option>
              <option value="mumbai">mumbai</option>
              <option value="delhi">delhi</option>
            </select>
            <br />
            <label htmlFor="languages">Languages</label>
            <input
              type="text"
              name="lang"
              onChange={this.onChange}
              value={this.state.lang}
            />
            <button
              className="btn btn-success"
              style={{ display: "inline", width: "20%" }}
              onClick={this.onClick}
            >
              Add
            </button>
            <div style={{ display: "inline", width: "20%" }}>
              {this.state.languages.length > 0 &&
                this.state.languages.map((ele, index) => {
                  return (
                    <a
                      href={`google.com/${ele}`}
                      className="btn btn-sm"
                      key={index}
                    >
                      {ele}
                    </a>
                  );
                })}
            </div>
            <br />
            <br />
            Cloud Exposure
            <br />
            <input
              type="checkbox"
              name="cloud"
              onChange={this.onChange}
              value="aws"
              style={{ margin: "10px" }}
            />
            AWS
            <input
              type="checkbox"
              name="cloud"
              onChange={this.onChange}
              value="gcp"
              style={{ margin: "10px" }}
            />
            GCP
            <input
              type="checkbox"
              name="cloud"
              onChange={this.onChange}
              value="azure"
              style={{ margin: "10px" }}
            />
            Azure
            <input
              type="checkbox"
              name="cloud"
              onChange={this.onChange}
              value="alibaba"
              style={{ margin: "10px" }}
            />
            Alibaba
            <input
              type="checkbox"
              name="cloud"
              onChange={this.onChange}
              value="iibm"
              style={{ margin: "10px" }}
            />
            IBM
            <br />
            <br />
            <label htmlFor="range" style={{ marginRight: "20px" }}>
              Expecting Raise
            </label>{" "}
            {this.state.raise} %
            <br />
            <input
              type="range"
              name="raise"
              value={this.state.raise}
              list="tickmarks"
              onChange={this.onChange}
            />
            <datalist id="tickmarks">
              <option value="0" label="0%"></option>
              <option value="10"></option>
              <option value="20"></option>
              <option value="30" label="30%"></option>
              <option value="40"></option>
              <option value="50"></option>
              <option value="60" label="60%"></option>
              <option value="70"></option>
              <option value="80"></option>
              <option value="90"></option>
              <option value="100" label="60%"></option>
            </datalist>
            <br />
            <label htmlFor="availability">Availability</label>
            <br />
            <input
              type="time"
              name="availability"
              value={this.state.availability}
              onChange={this.onChange}
            />
            <br />
            Bio <br />
            <textarea
              name="bio"
              value={this.state.bio}
              onChange={this.onChange}
              id="bio"
              cols="30"
              rows="10"
            ></textarea>
            <input
              className="btn btn-large btn-dark m-5"
              type="submit"
              value="submit"
            />
          </form>
        </div>
      </>
    );
  }
}

export default About;
